body{
  background-color: rgb(0,128,84);
}

.App{
  min-height: 100%;
  overflow: hidden;
}

.backImage{
  position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0.5;
}

.frontContent{
  z-index: 2;
  position: relative;
  height: 100%
}

#mapContainer {
    margin: 20px auto;
}
.navigation{
    height: 60px;
    display: flex;
    justify-content: space-between;
    background-color: black;
    width:100%;
    margin: 0px;
    padding:5px;
    box-shadow: 0px 10px 10px whitesmoke, 0px 10px 10px grey;
}

.Brand{
    font-size: 35px;
    padding-top: 5px;
}

.logo{
    color: rgb(0,128,84);
    display: inline;
    margin-left: 20px;

}
.controls {
    display: flex;
    min-width: 100%;
    margin: 0 auto;
    justify-content: space-evenly;
}
.controlBar{
    min-width: 100%;
    margin-bottom: 5px;
}

button {
    background-color: black;
    border-color: black;
    height: 100%;
    width: 100px;
    font-size: 20px;
    color: rgb(250,214,23);
    box-shadow: 2px 2px 10px rgb(250,214,23);
    border-radius: 6px;
}
